import { type ClubMemberForAuth } from 'src/models/ClubMember'
import { type User } from 'src/models/user'

import { type AuthStateUserObject } from 'react-auth-kit/dist/types'

const useUpdateAuthState = () => {
  return (user: User | AuthStateUserObject | null, clubs: ClubMemberForAuth[]) =>
    localStorage.setItem('_auth_state', JSON.stringify({ ...user, clubs }))
}

export default useUpdateAuthState
