import axios, { AxiosResponse } from 'axios'
import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'

import {
  Box,
  Button,
  FormControl,
  InputLabel,
  ListSubheader,
  MenuItem,
  Modal,
  TextField as MuiTextField,
  Select,
  type SelectChangeEvent,
  TextFieldProps,
  Typography,
  styled
} from '@mui/material'

import departments from 'src/data/departments'
import useUpdateAuthState from 'src/hooks'
import { type ClubMemberForAuth } from 'src/models/ClubMember'
import { type User } from 'src/models/user'

interface UserDataModalProps {
  open: boolean
  setOpen: (open: boolean) => void
  onClose: (event: React.MouseEvent<HTMLElement>, reason: 'backdropClick' | 'escapeKeyDown') => void
  setUser: (user: User) => void
  nickname: string
  name: string
  department: string
  phoneNumber: string
}

interface UpdateUserAPIResponse extends AxiosResponse {
  nickname: string
  name: string
  department: string
  phoneNumber: string
}

interface GetUserResponse extends AxiosResponse {
  user: User
  clubs: ClubMemberForAuth[]
}

const TextField = (props: TextFieldProps) => <MuiTextField size="small" required fullWidth {...props} />

const ModalBox = styled(Box)(
  ({ theme }) => `
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    max-width: 360px;
    width: 90%;
    background-color: #fff;
    box-shadow: 24;
    padding: ${theme.spacing(4)};
  `
)

function UserDataModal({
  open,
  setOpen,
  onClose,
  setUser,
  nickname,
  name,
  department,
  phoneNumber
}: UserDataModalProps) {
  const authHeader = useAuthHeader()
  const updateAuthState = useUpdateAuthState()
  const [values, setValues] = useState({ nickname, name, department, phoneNumber })
  const { enqueueSnackbar } = useSnackbar()

  const handleChange = (
    event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement> | SelectChangeEvent<string>
  ): void => {
    const newValues = {
      ...values,
      [event.target.name]: event.target.value
    }
    setValues(newValues)
  }
  const handleSubmit: React.FormEventHandler<HTMLFormElement> = event => {
    event.preventDefault()
    axios
      .put<UpdateUserAPIResponse>(`${process.env.REACT_APP_APIHOST}/user`, values, {
        headers: { Authorization: authHeader() }
      })
      .then(() =>
        axios
          .get<GetUserResponse>(`${process.env.REACT_APP_APIHOST}/user`, {
            headers: { Authorization: authHeader() }
          })
          .then(response => {
            if (response?.data) {
              const { user, clubs } = response?.data
              updateAuthState(user, clubs)
              setUser(user)
              setOpen(false)
              enqueueSnackbar('수정 완료되었습니다. 일부 정보는 재로그인 시 반영됩니다.', {
                variant: 'success'
              })
            } else throw Error()
          })
          .catch(error =>
            enqueueSnackbar(error.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
              variant: 'error'
            })
          )
      )
      .catch(error =>
        enqueueSnackbar(error.response?.data?.message ?? '서버에서 오류가 발생한 것 같아요. 다시 시도해주세요', {
          variant: 'error'
        })
      )
  }

  useEffect(() => {
    setValues({ nickname, name, department, phoneNumber })
  }, [nickname, name, department, phoneNumber])

  return (
    <Modal
      open={open}
      onClose={onClose}
      aria-labelledby="userdata-modal-title"
      aria-describedby="userdata-modal-description"
    >
      <ModalBox>
        <Typography component="h1" variant="h3" sx={{ mb: 2, textAlign: 'center' }}>
          사용자 정보 입력 / 수정
        </Typography>
        <Box
          component="form"
          sx={{
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            '& .MuiTextField-root': { margin: 1 }
          }}
          onSubmit={handleSubmit}
        >
          <TextField
            id="nickname"
            name="nickname"
            type="text"
            label="닉네임"
            defaultValue={values.nickname}
            onChange={handleChange}
          />
          <TextField
            id="name"
            name="name"
            type="text"
            label="이름"
            defaultValue={values.name || ''}
            onChange={handleChange}
          />
          <TextField
            id="phone-number"
            name="phoneNumber"
            type="tel"
            label="전화번호"
            defaultValue={values.phoneNumber || ''}
            helperText="슬래시(-) 기호를 포함하여 입력하세요. (예) 010-0000-0000"
            inputProps={{ pattern: '[0-9]{3}-[0-9]{4}-[0-9]{4}' }}
            onChange={handleChange}
          />
          <FormControl sx={{ margin: 1, width: '100%' }} size="small" required>
            <InputLabel htmlFor="department">학과/학부</InputLabel>
            <Select
              id="department"
              name="department"
              label="학과/학부"
              defaultValue={values.department || ''}
              onChange={handleChange}
              fullWidth
            >
              {departments &&
                Object.keys(departments).map(college => [
                  <ListSubheader sx={{ fontWeight: 'bold' }}>{college}</ListSubheader>,
                  // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                  // @ts-ignore
                  departments[college] &&
                    // eslint-disable-next-line @typescript-eslint/ban-ts-comment
                    // @ts-ignore
                    departments[college].map(depart => [
                      <MenuItem value={college === depart ? depart : college + ' ' + depart}>{depart}</MenuItem>
                    ])
                ])}
            </Select>
          </FormControl>
          <Button type="submit" variant="contained" fullWidth sx={{ mt: 1 }}>
            정보 수정
          </Button>
          <Button variant="outlined" fullWidth onClick={() => setOpen(false)} sx={{ mt: 1 }}>
            취소
          </Button>
        </Box>
      </ModalBox>
    </Modal>
  )
}

export default UserDataModal
